import {Component, Input, Output, Inject, EventEmitter, ElementRef} from 'angular2/core';

import 'mustache';

import {PageModel, PageService} from '../../services/page';
import {ConfigService} from '../../services/config';
import {PageFrame} from '../../directives/page-frame';

@Component({
	selector: 'page-frame',
	template: `<iframe src="/proposals/page/{{config.proposal_id}}" pageframe (onPageLoad)="writeHtml()"></iframe>`,
	directives: [PageFrame]
})
export class PageView {
	private _el: HTMLElement;
	
	@Input() page: PageModel;
	@Input() proposal: Object;

	constructor(private el: ElementRef, private _pageService: PageService, @Inject(ConfigService) private config) {
		this._el = el.nativeElement;
	}

	writeHtml() {
		this._el.querySelector('iframe').contentWindow.document.body.innerHTML = Mustache.render(this.page.template, this.proposal);
	}
}